import React from "react"
import { graphql } from "gatsby"
import Seo from "../components/seo"
import Layout from "../components/layout"

const BlogPostTemplate = ({ data }) => {
  // `data` prop will be injected by the GraphQL layer based on the query below
  const { markdownRemark: post } = data // Destructuring the post data

  return (
    <div>
      <Seo title="Blog Post" />
      <Layout>
      <div key={post.frontmatter.slug}>
        <section class="container mx-auto p-4 w-1200">
            <div class="py-8 lg:py-8 ">
            <div className="flex justify-end">
                <button onClick={() => window.history.back()} className="text-black py-2 px-4 hover:underline">
                    Back To Blogs
                </button>
            </div>
            <article class="">
                <div class="mb-5 text-gray-500">
                <span class="text-sm">{post.frontmatter.date}</span>
                </div>
                <h1 class="mb-4 text-3xl lg:text-4xl tracking-tight font-extrabold text-gray-900 dark:text-white">{post.frontmatter.title}</h1>
                <div class="mt-6 prose lg:prose-xl dark:prose-dark">
                <div dangerouslySetInnerHTML={{ __html: post.html }} />                
                </div>
            </article>
            </div>
        </section>    
      </div>
      </Layout>
    </div>
  )
}

export default BlogPostTemplate

// Define the page query
export const query = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        slug
      }
    }
  }
`
